import React, { Component } from 'react'
import { Form, Button } from 'react-bootstrap'
import '../vendor/styles/pages/authentication.scss'

class Page2 extends Component {
  constructor(props) {
      super(props)
      props.setTitle('Login v3 - Pages')

      this.state = {
          credentials: {
              email: '',
              password: '',
              rememberMe: false
          }
      }
  }

    onValueChange(field, e) {
        this.setState({
            credentials: {
                ...this.state.credentials,
                [field]: field === 'rememberMe' ? e.target.checked : e.target.value
            }
        })
    }

    prevent(e) {
        e.preventDefault()
    }

  render() {
    return (
        <div className="authentication-wrapper authentication-3">
            <div className="authentication-inner">

                {/* Side container */}
                {/* Do not display the container on extra small, small and medium screens */}
                <div className="d-none d-lg-flex col-lg-8 align-items-center ui-bg-cover ui-bg-overlay-container p-5" style={{ backgroundImage: `url('${process.env.PUBLIC_URL}/img/bg/ws/ws-stadion.jpg')`}}>
                    <div className="ui-bg-overlay bg-dark opacity-50"></div>

                    {/* Text */}
                    <div className="w-100 text-white px-5">
                        <h1 className="display-2 font-weight-bolder mb-4" style={{ textAlign: 'center', paddingTop: '500px' }}>EVERY SPORT STARTS WITH W</h1>
                        <div className="text-large font-weight-light" style={{ textAlign: 'center' }}>
                            Sign up, tell us about your club and find your new team members
                        </div>
                    </div>
                    {/* /.Text */}
                </div>
                {/* / Side container */}

                {/* Form container */}
                <div className="d-flex col-lg-4 align-items-center theme-bg-white p-5">
                    {/* Inner container */}
                    {/* Have to add `.d-flex` to control width via `.col-*` classes */}
                    <div className="d-flex col-sm-7 col-md-5 col-lg-12 px-0 px-xl-4 mx-auto">
                        <div className="w-100">

                            {/* Logo */}
                            <div className="d-flex justify-content-center align-items-center">
                                <div className="ui-w-60">
                                    <div className="w-100 position-relative">
                                        <img src="/img/bg/ws/logo/logocolor.png" width="60"/>
                                    </div>

                                </div>
                            </div>
                            {/* / Logo */}

                            <h4 className="text-center text-lighter font-weight-normal mt-5 mb-0">Please tell us about your sport</h4>

                            {/* Form */}
                            <form className="my-5">
                                <Form.Group>
                                    <Form.Label>What Is Your Sport *</Form.Label>
                                    <Form.Control value={this.state.credentials.sport} onChange={e => this.onValueChange('sport', e)} />
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>Name of the Club / Team *</Form.Label>
                                    <Form.Control value={this.state.credentials.clubname} onChange={e => this.onValueChange('clubname', e)} />
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>Short Description <span className="text-muted">(anything that matters)</span></Form.Label>
                                    <Form.Control value={this.state.credentials.description} onChange={e => this.onValueChange('description', e)} />
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>Country *</Form.Label>
                                    <Form.Control value={this.state.credentials.country} onChange={e => this.onValueChange('country', e)} />
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>City *</Form.Label>
                                    <Form.Control value={this.state.credentials.city} onChange={e => this.onValueChange('city', e)} />
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>Postal Code</Form.Label>
                                    <Form.Control value={this.state.credentials.postalcode} onChange={e => this.onValueChange('postalcode', e)} />
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control value={this.state.credentials.address} onChange={e => this.onValueChange('postalcode', e)} />
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>Contact Phone</Form.Label>
                                    <Form.Control value="" onChange={e => this.onValueChange('postalcode', e)}/>
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>Website</Form.Label>
                                    <Form.Control value={this.state.credentials.website} />
                                </Form.Group>

                                <Form.Group>
                                    <div className="d-flex justify-content-between align-items-center m-0">
                                        <Button variant="primary">Next</Button>
                                    </div>
                                </Form.Group>
                            </form>
                            {/* / Form */}

                        </div>
                    </div>
                </div>
                {/* / Form container */}

            </div>
        </div>
    )
  }
}

export default Page2
